<template>
  <div>
    <PageHeader>
      <template slot="title">戻る</template>
      <template slot="content">案件詳細</template>
    </PageHeader>
    <template>案件情報の登録及び編集する画面です。</template>
    <template>この画面で当案件の見積・注文・納品・検収・請求情報を確認できます。</template>
    <el-divider></el-divider>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm"
             class="demo-ruleForm">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item prop="ProjectName">
            <el-input v-model="ruleForm.ProjectName" placeholder="案件名">
              <template slot="prepend">案件名</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item>
            <el-input v-model="ruleForm.ProjectCode" placeholder="案件コード" :disabled="true">
              <template slot="prepend">案件CD</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item prop="CustomerName">
            <el-autocomplete
              popper-class="my-autocomplete"
              v-model="ruleForm.CustomerName"
              :fetch-suggestions="querySearch"
              placeholder="得意先"
              style="width:100%"
              @select="handleSelect">
              <i
                class="el-icon-edit el-input__icon"
                slot="suffix"
                @click="handleIconClick">
              </i>
              <template slot-scope="{ item }">
                <div class="name">{{ item.value = item.CustomerName }} ({{ item.key = item.Personnel }})</div>
              </template>
              <template slot="prepend">得意先</template>
            </el-autocomplete>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item prop="Personnel">
            <el-input v-model="ruleForm.Personnel" placeholder="担当者">
              <template slot="prepend">担当者</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="16">
          <el-form-item prop="Synopsis">
            <el-input v-model="ruleForm.Synopsis" placeholder="案件詳細">
              <template slot="prepend">案件詳細</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row type="flex" :gutter="20" justify="start">
        <el-col :span="13">
          <el-form-item>
            <el-button-group>
              <!--              <el-button type="warning" @click="resetForm('ruleForm')">クリア</el-button>-->
              <el-button type="primary" @click="submitForm('ruleForm')" icon="el-icon-finished">登録</el-button>
              <!--              <el-button type="success" @click="toEstimateform('ruleForm')" icon="el-icon-edit-outline">見積作成</el-button>-->
              <el-button type="success" @click="toEstimateFormDialog('ruleForm')" icon="el-icon-edit-outline">見積作成
              </el-button>
              <el-button type="danger" @click="deleteForm('ruleForm')" icon="el-icon-document-delete">案件廃棄</el-button>
            </el-button-group>
          </el-form-item>
        </el-col>
      </el-row>
      <!--    引入見積書・注文書・納品書・検収書・請求書tab组件-->
      <el-row :gutter="20">
        <el-col :span="24">
          <ProjectTabTable :c-table-data-estimate="tableDataEstimate"
                           :c-table-data-order="tableDataOrder"
                           :c-table-data-delivery="tableDataDelivery"
                           :c-table-data-acceptance="tableDataAcceptance"
                           :c-table-data-invoice="tableDataInvoice"
                           @evEstimateForm="toEstimateFormDialog(arguments)"
                           @evOrderForm="toOrderFormDialog(arguments)"></ProjectTabTable>
        </el-col>
      </el-row>
    </el-form>
    <div>
      <!--      <el-dialog title="見積データの登録・更新" :visible.sync="dialogFormVisible" v-if="dialogFormVisible"-->
      <!--                 top="5%"-->
      <!--                 destroy-on-close>-->
      <!--        <ProjectTabForm :c-project-table-form="toProjectTableForm" @cancel="closeDialog"-->
      <!--                        @submit="reloadList"></ProjectTabForm>-->
      <!--      </el-dialog>-->
      <el-dialog title="見積書データの登録・更新" :visible.sync="dialogEstimateFormVisible" v-if="dialogEstimateFormVisible"
                 top="5%"
                 destroy-on-close>
        <EstimateForm :c-estimate-form="toEstimateForm"
                      @cancel="closeDialog"
                      @submit="reloadList"></EstimateForm>
      </el-dialog>
      <el-dialog title="注文書データの登録・更新" :visible.sync="dialogOrderFormVisible" v-if="dialogOrderFormVisible"
                 top="5%"
                 destroy-on-close>
        <OrderForm :c-order-form="toOrderForm"
                   @cancel="closeDialog"
                   @submit="reloadList"></OrderForm>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {getCustomerDataAll} from 'api/customers'
import {getEstimateDataAll} from 'api/estimates'
import {getProjectDataOne, postProjectData} from 'api/projects'
import {getOrderDataAll} from 'api/order'
import {getDeliveryDataAll} from 'api/delivery'
import PageHeader from 'components/common/menu/PageHeader'
import ProjectTabTable from 'components/content/project/ProjectTabTable'
// import ProjectTabForm from 'components/content/project/ProjectTabForm'
import EstimateForm from "@/views/project/EstimateForm";
import OrderForm from "@/views/project/OrderForm";

export default {
  name: 'ProjectForm',
  components:
    {
      PageHeader,
      ProjectTabTable,
      // ProjectTabForm
      EstimateForm,
      OrderForm
    },
  data() {
    return {
      dialogEstimateFormVisible: false,
      dialogOrderFormVisible: false,
      restaurants: [],
      Customers: [],
      ruleForm: {
        ProjectCode: '',
        ProjectName: '',
        CustomerName: '',
        CustomerCode: '',
        Personnel: '',
        Synopsis: ''
      },
      tableDataEstimate: [{}],
      tableDataOrder: [{}],
      tableDataDelivery: [{}],
      tableDataAcceptance: [{}],
      tableDataInvoice: [{}],
      toEstimateForm: {},
      toOrderForm: {},
      rules: {
        ProjectName: [
          {required: true, message: '案件名を入力してください', trigger: 'blur'}
        ],
        CustomerName: [
          {required: true, message: '得意先を選びまたは入力してください', trigger: 'change'}
        ],
        Synopsis: [
          {required: true, message: '案件詳細を入力してください', trigger: 'blur'}
          // { min: 20, max: 300, message: '20~300文字まで入力してください', trigger: 'blur' }
        ]
      }
    }
  },
  created: function () {
    this.getCustomerDataAll()
    this.getEstimateDataAll()
    this.getDeliveryDataAll()
    if (this.$route.query.ProjectCode) {
      this.getProjectDataOne()
      this.getOrderDataAll()
    } else {
      this.ruleForm.ProjectCode = this.newProjectcode('Prj')
    }
  },
  computed: {
    newDateTime() {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      var hours = date.getHours()
      var minutes = date.getMinutes()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = year + '-' + month + '-' + strDate + ' ' + hours + ':' + minutes

      return currentdate
    }
  },
  methods: {
    newProjectcode(parma) {
      let date = new Date()
      let year = date.getFullYear()
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      let hours = date.getHours()
      let minutes = date.getMinutes()
      let seconds = date.getSeconds()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 1 && strDate <= 9) {
        strDate = '0' + strDate
      }
      if (hours >= 0 && hours <= 9) {
        hours = '0' + hours
      }
      if (minutes >= 0 && minutes <= 9) {
        minutes = '0' + minutes
      }
      if (seconds >= 0 && seconds <= 9) {
        seconds = '0' + seconds
      }

      let datetime = year + month + strDate + hours + minutes + seconds
      let currentdate = parma + datetime.slice(2)

      return currentdate
    },
    getCustomerDataAll() {
      getCustomerDataAll().then(res => {
        this.Customers = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    getProjectDataOne() {
      let values = {}
      values['ProjectCode'] = this.$route.query.ProjectCode
      getProjectDataOne(values).then(res => {
        this.ruleForm.ProjectCode = res.data.data[0].ProjectCode
        this.ruleForm.ProjectName = res.data.data[0].ProjectName
        this.ruleForm.CustomerName = res.data.data[0].CustomerName
        this.ruleForm.Personnel = res.data.data[0].Personnel
        this.ruleForm.Synopsis = res.data.data[0].Synopsis
        this.ruleForm.CustomerCode = res.data.data[0].CustomerCode
      }).catch(err => {
        console.log(err)
      })
    },
    getEstimateDataAll() {
      let values = {}
      values['ProjectCode'] = this.$route.query.ProjectCode
      getEstimateDataAll(values).then(res => {
        this.tableDataEstimate = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    getOrderDataAll() {
      let values = {}
      values['ProjectCode'] = this.$route.query.ProjectCode
      getOrderDataAll(values).then(res => {
        this.tableDataOrder = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    getDeliveryDataAll() {
      let values = {}
      values['ProjectCode'] = this.$route.query.ProjectCode
      getDeliveryDataAll(values).then(res => {
        this.tableDataDelivery = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    postProjectData(isDelete) {
      let values = {}
      values['ProjectCode'] = this.ruleForm.ProjectCode
      values['ProjectName'] = this.ruleForm.ProjectName
      values['CustomerName'] = this.ruleForm.CustomerName
      values['CustomerCode'] = this.ruleForm.CustomerCode
      values['Personnel'] = this.ruleForm.Personnel
      values['Synopsis'] = this.ruleForm.Synopsis
      values['DateTime'] = this.newDateTime
      // isDelete传入'1'代表删除，传入'0'是更新
      values['Is_Delete'] = isDelete
      postProjectData(values).then(res => {
        this.tableData = res.data.data
        // 确保迁移到下一个画面数据是提交之后的
        if (res.data.code === '200') {
          this.$router.push({path: 'ProjectList'})
        }
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
          this.postProjectData(0)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    deleteForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
          this.postProjectData(1)
        } else {
          console.log('error submit!!')
          return false
        }
      })
      this.$router.push({path: 'ProjectList'})
    },
    // resetForm(formName) {
    //   this.$refs[formName].resetFields()
    // },
    toEstimateFormDialog(param) {
      this.dialogEstimateFormVisible = true
      if (param[0].uuidEstimate) {
        this.toEstimateForm = {
          ProjectCode: this.ruleForm.ProjectCode,
          ProjectName: this.ruleForm.ProjectName,
          CustomerName: this.ruleForm.CustomerName,
          CustomerCode: this.ruleForm.CustomerCode,
          EstimateCode: param[0].EstimateCode,
          uuid: param[0].uuidEstimate
        }
      } else {
        this.toEstimateForm = {
          ProjectCode: this.ruleForm.ProjectCode,
          ProjectName: this.ruleForm.ProjectName,
          CustomerName: this.ruleForm.CustomerName,
          CustomerCode: this.ruleForm.CustomerCode,
          EstimateCode: this.newProjectcode('Esh'),
          uuid: ''
        }
      }
      this.toEstimateForm
    },
    toOrderFormDialog(param) {
      this.dialogOrderFormVisible = true
      console.log(param[0]);
      if (param[0].uuidOrder) {
        this.toOrderForm = {
          ProjectCode: this.ruleForm.ProjectCode,
          ProjectName: this.ruleForm.ProjectName,
          CustomerName: this.ruleForm.CustomerName,
          CustomerCode: this.ruleForm.CustomerCode,
          EstimateCode: param[0].EstimateCode,
          OrderCode: param[0].OrderCode,
          uuid: param[0].uuidOrder
        }
      } else {
        this.toOrderForm = {
          ProjectCode: this.ruleForm.ProjectCode,
          ProjectName: this.ruleForm.ProjectName,
          CustomerName: this.ruleForm.CustomerName,
          CustomerCode: this.ruleForm.CustomerCode,
          EstimateCode: param[0].EstimateCode,
          OrderCode: this.newProjectcode('Ord'),
          uuid: ''
        }
      }
      console.log(param);
    },
    closeDialog() {
      this.dialogEstimateFormVisible = false
      this.dialogOrderFormVisible = false
    },
    reloadList() {
      this.dialogEstimateFormVisible = false
      this.dialogOrderFormVisible = false
      this.getEstimateDataAll()
      this.getOrderDataAll()
    },
    evEstimateForm(param) {
      this.dialogEstimateFormVisible = true
      console.log(param[0].uuidEstimate);
    },
    evOrderForm(param) {
      this.dialogOrderFormVisible = true
      console.log(param[0].uuidEstimate);
    },
    querySearch(queryString, cb) {
      var Customers = this.Customers
      var results = queryString ? Customers.filter(this.createFilter(queryString)) : Customers
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.CustomerName.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelect(item) {
      // 把选择的顾客名对应的顾客CD一并发送给后端
      this.ruleForm.CustomerCode = item.CustomerCode
      this.ruleForm.Personnel = item.Personnel
    },
    handleIconClick(ev) {
      console.log(ev)
    }
  }
}
</script>

