<script>
export default {
  name: 'ProjectTabTable',
  props: {
    cTableDataEstimate: {
      type: Array,
      default() {
        return []
      }
    },
    cTableDataOrder: {
      type: Array,
      default() {
        return []
      }
    },
    cTableDataDelivery: {
      type: Array,
      default() {
        return []
      }
    },
    cTableDataAcceptance: {
      type: Array,
      default() {
        return []
      }
    },
    cTableDataInvoice: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      activeName: ''
    }
  },
  created: function () {
    let active = this.$route.query.activeName
    if (active) {
      this.activeName = active
    } else {
      this.activeName = 'first'
    }
  },
  methods: {
    handleEditEstimate(index, row) {
      this.$emit('evEstimateForm', {uuidEstimate: row.uuid, EstimateCode: row.EstimateCode})
    },
    toOrderForm(index, row) {
      this.$emit('evOrderForm', {EstimateCode: row.EstimateCode})
    },
    handleEditOrder(index, row) {
      this.$emit('evOrderForm', {uuidOrder: row.uuid, EstimateCode: row.EstimateCode, OrderCode: row.OrderCode})
    },
    handleEditDelivery(index, row) {
      this.$router.push({
        path: 'Deliveryform',
        query: {
          DeliveryCode: row.DeliveryCode,
          EstimateCode: row.EstimateCode
        }
      })
    },
    handleClick(tab, event) {
      console.log(tab, event)
    }
  }
}
</script>

